import * as React from 'react';
import { IconButton } from './Basics';
import { IconLink, IconDeleteLink } from '../icons';
import type { IIconButtonProps } from './Basics';

export function DeleteLinkButton(props: IIconButtonProps) {
  const {
    icon = <IconDeleteLink />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="DeleteLinkButton"
      icon={icon}
      {...rest}
    />
  )
}

export function AddLinkButton(props: IIconButtonProps) {
  const {
    icon = <IconLink />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="AddLinkButton"
      icon={icon}
      {...rest}
    />
  )
}

